export function EventDemo() {

    function clickHandler1(e){
      alert('Hello Event!!!'+e.target.textContent)
    }
    function clickHandler2(a,b){
      alert(a+b);
    }
    return (
      <div>
        <h2>Event Demo</h2>
        <button onClick={clickHandler1}>Click Me!!</button>
        <button onClick={()=> clickHandler2(10,20)}>Greet</button>
  
      </div>
    )
  }