import React from 'react';
export function StateDemo(){
    let [msg,setMsg] = React.useState('Hello IBM');

    
       let handleClick = () => {
           setMsg('Hello React!!');
           
    }
        return (
            <div>
                <h2>State Demo</h2>
                <h4>Message : {msg}</h4>
                <button onClick={handleClick}>click me!!</button>
            </div>
        )

}