import React from "react";
export class Signup extends React.Component{
    constructor()
    {
        super();
        this.state = {
            name:'',
            email:''
        }
    }
    doSignup = ()=>{
        alert(this.state.name)
    }
    datachanged = (e)=>{
        this.setState({
            [e.target.name]:[e.target.value]
        });
    }
    
    render(){
        return (
            <div>
                <h2>SignUp</h2>
                <div>
                    <label>Name</label>
                    <input type='text' name='name' onChange={this.datachanged}></input>
                </div>
                <div>
                    <label>Email</label>
                    <input type='text' name='email' onChange={this.datachanged} ></input>
                </div>
                <div>
                    <button onClick={this.doSignup}>Signup</button>
                </div>
                {this.state.name},{this.state.email}

            </div>
        )
    }
}