import React from 'react';
import {createRef} from 'react';
export function Topics(){
    let n1ref = createRef();
    let [tps,setTps] = React.useState(['']);

    
       let handleClick = () => {
        setTps([...tps,n1ref.current.value]);
           
    }
        return (
            <div>
                <h2>Topics</h2>
                <label>Enter Topic</label><input type='type' name='skill' ref={n1ref}></input>
                <button onClick={handleClick}>click me!!</button>
                <ol>
                    {tps.map((t)=><li>{t}</li>)}
                </ol>
                
            </div>
        )

}